}

.fc_parentchild li.arrowdown {
    position: relative;
    margin-bottom: 0px;
    padding-bottom: 60px;
    background-image: url(/Content/OrganizationChart/FlowChart_DownArrow_90.png);
    background-repeat: no-repeat;
    background-position: 129px bottom;
}

.flowfsm .fc_parentchild li.arrowdown:after {
    position: absolute;
    top: 67%;
