<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('Result') }}</h4>
                        <button type="button" class="close" @click="$emit('close')">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="ManageRuleEngineTarget">
                            <div class="DivRuleFlowDataShowCls flowfsm" id="divruleflowdatafsmshow">
                                <div class="fc_parentchild">
                                    <div class="row">
                                        <div class="col-md-12 arrowdown">
                                            <div class="py-3 px-3 bg-color-yellow">
                                                <select id="ddlwarningMessageTypeId"
                                                    v-on:change="OnChangeEvent(items, $event, index)"
                                                    name="ddlwarningMessageTypeId"
                                                    class="form-control customddl ddlwarningMessageTypeId inline-block text-truncate">
                                                    <option v-for="item in GetEventResultTypeDropdown" :value="item.value">
                                                        {{ $t(item.text) }}
                                                    </option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="clstargetadd div-li mt-4 arrowdown" id="li_0" v-for="(item, index) in Mainleveldiv" :key="index">
                                        <div class="formsfieldmainunique_condition">
                                            <div class="innercontbase">
                                                <div class="row" v-if="ShowSendResponsediv">
                                                    <div class="col-md-6" v-if="IsShowdisableField == true">
                                                        <div class="innercont w-100 ml-1" v-if="CheckExtraFeatures == true">
                                                            <select class="form-control ddlresultCriteria" v-model="item.TargetSelectValue"
                                                                v-on:change="OnChangeField(items, $event)">
                                                                <option value="">{{ $t('Select') }}</option>
                                                                <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                </option>
                                                                <option value="department">{{ $t('Department') }}</option>
                                                                <option value="experience">{{ $t('Experience') }}</option>
                                                                <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                </option>
                                                                <option value="skill">{{ $t('Skill') }} </option>
                                                                <option value="User">{{ $t('Users/Technician') }}</option>
                                                            </select>
                                                        </div>
                                                        <div class="innercont w-100 ml-1"
                                                            v-if="CheckExtraFeatures == false">
                                                            <select class="form-control ddlresultCriteria" v-model="item.TargetSelectValue"
                                                                v-on:change="OnChangeField(items, $event, index)">
                                                                <option value="">{{ $t('Select') }}</option>
                                                                <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                </option>
                                                                <option value="department">{{ $t('Department') }}</option>
                                                                <option value="experience">{{ $t('Experience') }}</option>
                                                                <option value="locatorarea">{{ $t('LocatorArea') }}</option>
                                                                <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                </option>
                                                                <option value="skill">{{ $t('Skill') }} </option>
                                                                <option value="User">{{ $t('Users/Technician') }}</option>
                                                            </select>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6" v-if="IsShowdisableField == false">
                                                        <div class="innercont w-100 ml-1">
                                                            <select class="form-control ddlresultCriteria disabled"
                                                                disabled="disabled">
                                                                <option value="">{{ $t('Select') }}</option>
                                                                <option value="offhours">{{ $t('BasedOnOffHours') }}
                                                                </option>
                                                                <option value="department">{{ $t('Department') }}</option>
                                                                <option value="experience">{{ $t('Experience') }}</option>
                                                                <option value="locatorarea">{{ $t('LocatorArea') }}</option>
                                                                <option value="serviceboard">{{ $t('ServiceBoard') }}
                                                                </option>
                                                                <option value="skill">{{ $t('Skill') }} </option>
                                                                <option value="User" selected>{{ $t('Users/Technician') }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>

                                                    <div class="col-md-6 mt-3" v-if="SelectAssignTo == true">
                                                        <div class="input-group">
                                                            <select class="form-control ddlTargetResult" data-id="1"
                                                                id="ddl_1" v-if="IsShowDdlTarget" v-model="item.selectedValue">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option v-for="item in item.DDlType" :value="item.value">{{
                                                                    $t(item.text) }}
                                                                </option>
                                                            </select>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2"
                                                                v-if="IsShowAddBtn && index==0">
                                                                <a class="d-block ancaddresultrow" style="cursor: pointer;"
                                                                    data-placement="left" data-index="0" href="javascript:;"
                                                                    title="Add" @click="AddMoreData()">
                                                                    <i class="fa f30 fa-plus-circle"></i>
                                                                </a>
                                                            </span>
                                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2" v-if="index != 0">
                                                        <a id="idd_2" class="btn btn-danger text-white deleteresultrow" @click="DeleteUser(index)" title="Delete"><i class="fa f30 fa-minus-circle"></i></a>
                                                    </span>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6 mt-3" v-if="SelectAssignTo == false">
                                                        <div class="input-group">
                                                            <multiselect v-model="SelectedUser" id="ddlfirstfield"
                                                                :limit="1" name="ddlfirstfield" label="text"
                                                                track-by="value" v-bind:options="GetCompanyUserByCompanyId"
                                                                v-bind:multiple="true" v-bind:taggable="true">
                                                            </multiselect>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="row" v-if="SelectAssignTo == false && ShowSendResponsediv">
                                                    <div class="col-md-6">
                                                        <div class="innercont w-100 ml-1">
                                                            <select class="form-control ddlTargetResult" data-id="1"
                                                                id="ddl_1" v-if="IsShowDdlTarget">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option v-for="item in GetEmailTemplateDDlList"
                                                                    :value="item.value">{{
                                                                        $t(item.text) }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6" v-if="SelectCloseTicketTo">
                                                        <div class="innercont w-100 ml-1">
                                                            <select class="form-control ddlTargetResult" data-id="1"
                                                                id="ddl_1" v-if="IsShowDdlTarget">
                                                                <option value>{{ $t("Select") }}</option>
                                                                <option v-for="item in GetNotificationDDlList"
                                                                    :value="item.value">{{
                                                                        $t(item.text) }}
                                                                </option>
                                                            </select>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-md-12 text-right mt-3">
                                    <a id="btnSaveTargetResult" class="btn btn-success mr-2 mt-3" title="Save"><i
                                            class="fa fa-save pr-2"></i><span>{{ $t("Save") }}</span></a>
                                    <a id="ancTargetResultDialog" v-on:click="Close()" class="btn btn-danger mt-3"
                                        title="Cancel"><i class="fa fa-close pr-2"></i><span>{{ $t("Cancel") }}</span></a>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from "../../services/DataService";
export default {
    props: {
        acionId: {
            type: Number,
            required: true
        },
        eventId: {
            type: Number,
            required: true
        },
        moduleGroupId: {
            type: Number,
            required: true
        },
        moduleName: {
            type: String,
            required: true
        },
        TargetConditionData: {
            type: Array,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            CheckExtraFeatures: false,
            GetEventResultTypeDropdown: [],
            Mainleveldiv: [{ selectedValue:'', DDlType:'',TargetSelectValue:''}],
            GetDepartmentListbyCompanyForClient: [],
            GetSkillTypeList: [],
            GetExpierenceTypeList: [],
            GetTicketChainList: [],
            GetCompanyUserByCompanyId: [],
            GetEmailTemplateDDlList: [],
            GetNotificationDDlList: [],
            IsShowDdlTarget: true,
            IsShowAddBtn: true,
            SelectAssignTo: true,
            SelectCloseTicketTo: false,
            ShowSendResponsediv: true,
            IsShowdisableField: true
        }
    },
    created: async function () {
        var vm = this;
        await vm.GetEventResultType(vm.eventId);
        await vm.CheckFeaturesForCompany();
        await vm.FetchData();
        this.GetSkillListing();
        this.GetResolveTicketListing();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `actionId=${vm.moduleGroupId}&eventids=${vm.eventids}&moduleName=${vm.moduleName}`;
            await DataService.AddTarget(url).then(function (response) {
                let GetTicketChain = [];
                GetTicketChain = response.data.GetTicketChainForDropdownListByCompanyId;
                GetTicketChain.forEach((item, index) => {
                    vm.GetTicketChainList.push({
                        value: item.ticket_chain_id,
                        text: item.ticket_chain_name
                    })
                });
                let GetExpierenceList = [];
                GetExpierenceList = response.data.GetExperienceList;
                GetExpierenceList.forEach((item, index) => {
                    vm.GetExpierenceTypeList.push({
                        value: item.ID,
                        text: item.EXPERIENCE
                    })
                });
                let GetEmailTemplate = [];
                GetEmailTemplate = response.data.GetEmailTemplateList;
                GetEmailTemplate.forEach((item, index) => {
                    vm.GetEmailTemplateDDlList.push({
                        value: item.TEMPLATE_ID,
                        text: item.TEMPLATE_NAME
                    })
                });
                let GetNotification = [];
                GetNotification = response.data.GetNotificationType;
                GetNotification.forEach((item, index) => {
                    vm.GetNotificationDDlList.push({
                        value: item.ID,
                        text: item.TYPE_NAME
                    })
                });
                let GetDepartmentList = [];
                GetDepartmentList = response.data.GetDepartmentListbyCompanyForClient.result;
                GetDepartmentList.forEach((item, index) => {
                    vm.GetDepartmentListbyCompanyForClient.push({
                        value: item.departmenT_ID,
                        text: item.departmenT_NAME
                    })
                });
            })
        },
        CheckFeaturesForCompany: async function () {
            var vm = this;
            var appendingUrl = `featureCode=${'ACCOUNTSETUP'}`;
            await DataService.CheckExtraFeatures(appendingUrl).then(function (response) {
                if (response.data == true) {
                    vm.CheckExtraFeatures = true;
                } else {
                    vm.CheckExtraFeatures = false;
                }
            });
        },
        GetSkillListing: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'USER_SKILLS'}&search=&refId=`;
            await DataService.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.GetSkillTypeList.push({
                                value: item.value,
                                text: item.name
                            })
                        })
                    }
                }
            });
        },
        GetResolveTicketListing: async function () {
            var vm = this;
            var params = `departmentId=&skillId=`
            await DataService.GetResolveTicketUserList(params).then(function (response) {
                debugger;
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.GetCompanyUserByCompanyId.push({
                                value: item.userId,
                                text: item.userName
                            })
                        })
                    }
                }
            });
        },
        GetEventResultType: async function (EventId) {
            var vm = this;
            var appendingUrl = `eventId=${EventId}`;
            await DataService.GetEventResultType(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        json.forEach((item, index) => {
                            vm.GetEventResultTypeDropdown.push({
                                value: item.RESULT_ID,
                                text: item.DISPLAY_NAME
                            })
                        })
                    }
                }
            });
        },
        AddMoreData() {
            var vm = this;
            debugger;
            vm.Mainleveldiv.push({
                DDlType: '',
                TargetSelectValue: '',
                selectedValue: '',
            })
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.Mainleveldiv.splice(id, 1);
        },
        //     CheckCriteriaExist: async function (selectedValue, ctrl) {
        //     var isSelected = false;
        //     $("#divruleflowdatafsmshow select.ddlresultCriteria").not(ctrl).each(function () {
        //         //debugger;
        //         if ($(this).val() == selectedValue) {
        //             isSelected = true;
        //         }
        //     });
        //     return isSelected;
        // },
        OnChangeField: async function (item, event, index) {
            //debugger;
            var vm = this;
            var selectedValue = event.target.value;
            vm.IsShowDdlTarget = true;
            // if (CheckCriteriaExist(selectedValue, $(this)) == false) {
            // var ctrl = $(this).closest(".div-li").find("select.ddlTargetResult");
            // var ctrl1 = $(this).closest(".div-li").find(".sourcediv");
            if (selectedValue == "department") {
                vm.Mainleveldiv[index].DDlType =vm.GetDepartmentListbyCompanyForClient;
                //vm.DDlType = vm.GetDepartmentListbyCompanyForClient;
                vm.IsShowAddBtn = true;
            }
            else if (selectedValue == "skill") {
                vm.Mainleveldiv[index].DDlType =vm.GetSkillTypeList;
               // vm.DDlType = vm.GetSkillTypeList;
                vm.IsShowAddBtn = true;
            }
            else if (selectedValue == "experience") {
                vm.Mainleveldiv[index].DDlType =vm.GetExpierenceTypeList;
                //vm.DDlType = vm.GetExpierenceTypeList;
                vm.IsShowAddBtn = true;
            }
            else if (selectedValue == "serviceboard") {
                vm.Mainleveldiv[index].DDlType =vm.GetTicketChainList;
                //vm.DDlType = vm.GetTicketChainList;
                vm.IsShowAddBtn = true;
            }
            else if (selectedValue == "User") {
                if(index>0){
                    vm.Mainleveldiv[index].TargetSelectValue='';
                    vm.ShowAlert(vm.$t('cannotcombineresultconditions'), 'warning', true, vm.$t('Alert'));
                }else{
                vm.Mainleveldiv[index].DDlType =vm.GetCompanyUserByCompanyId;
                vm.IsShowAddBtn = true;
            }
            }
            else if (selectedValue == "locatorarea" || selectedValue == "offhours") {
                if(index>0){
                    vm.Mainleveldiv[index].TargetSelectValue='';
                    vm.ShowAlert(vm.$t('cannotcombineresultconditions'), 'warning', true, vm.$t('Alert'));
                }else{
                vm.IsShowDdlTarget = false;
                vm.IsShowAddBtn = false;
                }
            }
            // }
            // else {
            //     $(this).val('');
            //     ShowAlert(Globalize.localize("ServiceSameFieldMessage"), "failure", true, Globalize.localize("Alert"));
            // }
        },
        OnChangeEvent: async function (item, event) {
            var vm = this;
            var selectedValue = event.target.selectedOptions[0].innerHTML.trim();
            if (selectedValue == "Send Email Notification") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = false;
            }
            else if (selectedValue == "Close Ticket") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = false;
                vm.SelectCloseTicketTo = false;
                vm.IsShowdisableField = false;
            }
            else if (selectedValue == "Send Response To Call center") {
                vm.ShowSendResponsediv = false;
            }
            else if (selectedValue == "Assign To") {
                vm.ShowSendResponsediv = true;
                vm.SelectAssignTo = true;
                vm.SelectCloseTicketTo = true;
                vm.IsShowdisableField = true;
            }
        },
        Close() {
            var vm = this;
            this.$emit("close");
        },
        btnSaveTarget() {
            var vm = this;
            vm.$refs.ManageRuleEngineTarget.validate().then(result => {
                if (result) {
                    var data = vm.Mainleveldiv;
                    var TargetCondition = vm.RuleCondition;
                    var TargetDataBaseCondition = vm.RuleDataBaseCondition;
                    vm.$emit('GetformFields', data, TargetCondition, TargetDataBaseCondition);
                    vm.Close();
                }
            })
        }
    }
}
</script>

